<template>
  <view>
    <view class="new-class" @click="gotoNewClass">
      <view class="text-box">
        <text>创建班级</text>
        <text style="color: dimgray;">邀请学生加入</text>
      </view>
      <image :src="classImage" mode="widthFix" style="width: 160px;"></image>
    </view>
    <view class="title-box">
      管理的班级
    </view>
    <view class="classes">
      <view class="class-item" v-for="(item, index) in classes" :key="index" :style="{backgroundColor: colors[index%4]}">
        <view class="text">{{item}}</view>
      </view>
    </view>
  </view>
</template>

<script>
  export default {
    data() {
      return {
        classes: ["19科技1班", "19科技2班", "19科技3班", "19科技4班"],
        classImage: '',
        colors: ['rgba(239, 192, 192,0.48)','rgba(119, 212, 227, 0.37)','rgba(124, 194, 245, 0.4)','rgba(224, 189, 243, 0.37)']
      };
    },
    onLoad() {
      this.getServiceData()
    },
    methods: {
      // 获取提供的服务图标和名称
      async getServiceData() {
        const res = await uni.$http.get(
          'http://10.12.26.159:5000/api/classesmanage/classimage') //10.12.26.159为该电脑ipv4地址，使用localhost或127.0.0.1用手机预览获取不到数据，使用ipv4地址才可以
        // 请求失败
        if (res.statusCode !== 200) return uni.$showMsg()
        this.classImage = res.data.classimage
      },
    }
  }
</script>

<style lang="scss" scoped>
  .new-class {
    display: flex;
    width: 280px;
    height: 110px;
    padding: 16px;
    margin: 14px auto;
    font-size: 16px;
    text-align: center;
    background-color: rgba(150,198,219,0.48);
    border-radius: 18px;
    box-shadow: 0 6px 18px 0 rgba(0, 0, 0, 0.1);
  }
  .text-box {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 10px;
  }
  .title-box {
    font-size: 20px;
    padding-left: 14px;
  }
  .classes {
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
  }
  .class-item {
    width: 46%;
    height: 80px;
    margin: 5px;
    text-align: center;
    border-radius: 18px;
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.1);
  }
  .text {
    margin: 28px auto;
  }
</style>
